// Button
.btn-hollow {
	display: inline-block;
	padding: 6px 12px;
	margin-bottom: 0;
	font-size: 14px;
	font-weight: normal;
	line-height: 1.42857143;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	-ms-touch-action: manipulation;
	touch-action: manipulation;
	cursor: pointer;
	background-image: none;
	border: 1px solid transparent;
	border-radius: 0;
	box-shadow: none;
	outline: none;
	&:focus {
		outline: none;
	}
	&.hollow-black {
		background-color: #000;
		color: #fff;
		&:hover {
			background-color: #fff;
			color: #000;
			border-color: #000;
			text-decoration: none;
		}
	}
	&.btn-hollow-sm {
		padding: 5px 10px;
		font-size: 12px;
	}
	&.btn-hollow-xs {
		padding: 3px 6px;
		font-size: 10px;
	}
}
.hollow-social {
	width: 38px;
	height: 35px;
	&.hollow-white {
		background-color: #fff;
		color: #000;
		&:hover {
			background-color: #fff;
			color: #000;
			border-color: #000;
			text-decoration: none;
		}
	}
	&.btn-hollow-xs {
		width: 30px;
		height: 28px;
		font-size: 12px;
	}
}
.dropdown-menu.category-list {
	margin-top: 30px;
	width: 180px;
	background-color: #000;
	li {
		padding: 8px;
		padding-left: 10px;
		font-size: 12px;
		border-bottom: 1px solid #666;
		&:last-child {
			border-bottom: none;
		}
		a {
			background-color: #000;
			color: #bbb;
			&:hover {
				background-color: #000;
				color: #fff;
			}
		}
	}
}
input.text-hollow {
	border: none;
	border-bottom: 1px solid #333;
	margin-right: 18px;
	outline: none;
	&:focus {
		outline: none;
		box-shadow: none;
	}
}

.hollow-pagination ul.pagination {
	border-radius: 0px;
	li span, li a {
		border: none;
		color: #aaa;
		&:hover {
			background-color: inherit;
			color: #000;
		}
	}
	li.active span {
		background-color: inherit;
		border-color: inherit;
		color: #000;
		font-weight: bold;
	}
}
.panel-hollow {
	margin-bottom: 20px;
	background-color: #fff;
	border: 1px solid #ddd;
	border-radius: 0;
	/*-webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, .05);
	box-shadow: 0 1px 1px rgba(0, 0, 0, .05);*/
	input {
		outline: none;
		border: 1px solid #ddd;
		border-radius: 0;
		box-shadow: none;
	}
	h1, h2, h3, h4, h5 {
